.Add {
  display: -ms-flexbox;
  display: flex;
  height: 530px;
  background-image: -webkit-linear-gradient(
    -76.044deg,
    rgb(109, 255, 198) 1%,
    rgb(63, 210, 206) 99%
  );
  background-image: -ms-linear-gradient(
    -76.044deg,
    rgb(109, 255, 198) 1%,
    rgb(63, 210, 206) 99%
  );
}

.Add_left {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-preferred-size: 45%;
      flex-basis: 45%;
  margin: 2rem;
}

.Add_left .Add_wrapper {
  position: relative;

  right: -10%;
  bottom: -15%;
}

.Add_left .Add_wrapper img {
  max-width: 100%;
  width: auto;
}

.Add_right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-preferred-size: 55%;
      flex-basis: 55%;
}

.Add_right .Add_wrapper {
  -ms-flex-negative: 1;
      flex-shrink: 1;
  max-width: 70%;
}

.Add_right h2 {
  font-weight: 400;
  font-size: 25px;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 0;
}

.Add_right h3 {
  font-weight: 500;
  font-size: 15px;
  color: #243638;
}

.Add_right p {
  font-weight: 400;
  font-size: 13px;
  color: #ffffff;
}

@media screen and (max-width: 568px) {
  .Add {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    height: auto;
  }

  .Add_right {
    -ms-flex-align: center;
        align-items: center;
  }

  .Add_right .Add_wrapper {
    max-width: 80%;
    text-align: center;
  }

  .Add_left .Add_wrapper {
    right: 0;
  }
}



