.Quote {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-linear-gradient(
    -89.706deg,
    rgb(255, 108, 84) 1%,
    rgb(255, 43, 89) 99%
  );
  background-image: -ms-linear-gradient(
    -89.706deg,
    rgb(255, 108, 84) 1%,
    rgb(255, 43, 89) 99%
  );
  text-align: center;
  height: 370px;
}

.Quote img {
  width: auto;
}

.Quote h2 {
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
}

.Quote h3 {
  font-weight: 400;
  font-size: 13px;
  color: #ffffff;
  margin: 0;
}

.Quote_arrow {
  padding: 20px 15px;
}



