.ContactUs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 657px;
  background-image: -webkit-linear-gradient(
    -84.202deg,
    rgb(255, 108, 84) 1%,
    rgb(255, 43, 89) 99%
  );
  background-image: -ms-linear-gradient(
    -84.202deg,
    rgb(255, 108, 84) 1%,
    rgb(255, 43, 89) 99%
  );
}

.ContactUs_wrapper {
  max-width: 40%;
  text-align: center;
}

.ContactUs h2 {
  font-weight: 400;
  font-size: 40px;
  color: #ffffff;
}

.ContactUs p {
  font-weight: 300;
  font-size: 13px;
  color: #ffffff;
}

.ContactUs_formWrapper {
  width: 100%;
  max-width: 40%;
  margin-top: 80px;
}

.ContactUs_form input {
  border: 1px solid #fff;
  background: none;
  padding: 15px 28px;
  font-size: 15px;
  font-weight: 500;
  color: #ffffff;
  width: 100%;
}

.ContactUs_form .halfWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 80px;
}

.ContactUs_form .halfWrapper input {
  width: 48%;
}

.ContactUs_form button {
  font-weight: 400;
  font-size: 15px;
  background: var(--statsBgColor);
  color: #fff;
  border: none;
  padding: 15px 35px;
  margin-top: 30px;
  float: right;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff;
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff;
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #ffffff;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #ffffff;
}

@media screen and (max-width: 1023px) {
  .ContactUs_wrapper {
    max-width: 80%;
  }
  .ContactUs_formWrapper {
    max-width: 80%;
  }
}

@media screen and (max-width: 568px) {
  .ContactUs_form .halfWrapper {
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 0;
  }
  .ContactUs_form .halfWrapper input {
    width: 100%;
    margin-bottom: 30px;
  }
  .ContactUs_form button {
    width: 100%;
  }
}



