/* Services */

#serviceSection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-height: 600px;
  padding: 2rem;
}

.Services {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Services_card {
  max-width: 271px;
  max-height: 276px;
  overflow: hidden;
  text-align: center;
  margin: 0 20px;
  margin-bottom: 90px;
}

.Services_card img {
  width: 129px;
  height: 129px;
}

.Services_card h2 {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 15px;
  color: var(--darkGray);
  margin-bottom: 2.5rem;
}

.Services_card p {
  font-size: 13px;
  color: var(--lightGray);
}

@media screen and (max-width: 768px) {
  .Services_card {
    margin-bottom: 40px;
  }
}



